table {
    width: 100%;
    border-top: solid;
    border-width: 3px;
    border-bottom: solid;
    border-color: grey;
    margin: auto;
    margin-top: 30px;
    overflow-x: auto;
    width: 100%;
    text-align: center;
}

tr {
    height: 50px;
}
th, td {
    text-align: center;
}

h1 {
    text-align: center;
    background-color: navy;
    color: white;
    padding-top: 20px;
    padding-bottom: 20px;
}

#searchBox {
    text-align: center;
}
